import { Component, OnInit } from '@angular/core';
import { Doctor } from '../../../interface/doctor';
import { LandingPageData } from '../../../interface/landingData';
import { Patient } from '../../../interface/patient';
import { orderDetails } from '../../../interface/orderDetails';

import { BaseHttpService } from '../../../services/base-http.service';
import { DoctorService } from '../../../services/doctor-service/doctor.service';
import { ActivatedRoute, Router } from '@angular/router';
import { CustomIconsService } from 'src/app/shared/UI_service/custom-icons.service';
import * as moment from "moment";

@Component({
  selector: 'app-your-heal-cart',
  templateUrl: './your-heal-cart.component.html',
  styleUrls: [
    './your-heal-cart.component.less',
    './style/add-to-cart.less'
  ]
})
export class YourHealCartComponent implements OnInit {



  constructor(private router: Router, private activedRouter: ActivatedRoute, private doctorService: DoctorService, private http: BaseHttpService, private CustomIcons: CustomIconsService) {

  }

  public tab: number = 2;
  selectedPatient: Patient;
  doctor: Doctor;
  isSpining: boolean = false;
  labCart: orderDetails[] = [];
  customStyle = {
    'background-color': '#fff',
    'border-radius': '4px',
    'margin-bottom': '10px',
  }

  ngOnInit(): void {

    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.doctorService.data$.subscribe((d: LandingPageData) => {
      this.doctor = d.doctor;
     this.getHealCart(this.selectedPatient.patient_id);

    })


  }

  patientChanged(item:Patient){

    if(this.selectedPatient.patient_id != item.patient_id) {
    this.getHealCart(item.patient_id);
    this.selectedPatient = item;

    }
  }


  getHealCart(patientId: string) {
    this.isSpining = true;

    const data = { "patientId": patientId, "doctorId": this.doctor.uid }
    this.http.makePartnerRequest<orderDetails[]>({ method: 'POST', url: '/v1/prescription/getPatientCart', data: data }).subscribe(res => {
      if(res.errors) {
        this.isSpining = false;
        return false;
      }      
      
      this.isSpining = false;
      this.labCart = res.data;
    })


  }

  itemsAddedCart: orderDetails[] = [];
  addItemToCart(orderItem: orderDetails) {

    const found = this.itemsAddedCart.some((el) => {
      return el.prescriptionId === orderItem.prescriptionId;

    })

    if (!found) {
      orderItem.isAddedTocart = true;
      orderItem.selectedSlotDateTime = {selectedDate:moment(new Date()).format("yyyy-MM-DD"),slotStartTime:'',slotEndTime:''}
      this.itemsAddedCart.push(orderItem);
    }


  }

  processToCheckout() {

    if (this.itemsAddedCart.length > 0) {

      localStorage.setItem('cartData-'+this.selectedPatient.patient_id,JSON.stringify(this.itemsAddedCart));

      this.router.navigate(['./confirm-order'], {
        relativeTo: this.activedRouter
      });

    }
  }


}
