import { Component, OnInit } from '@angular/core';
import { Appointment } from '../../../interface/appointment';
import { BaseHttpService } from '../../../services/base-http.service';
import { HttpConstants } from '../../../services/http-constants';
import { todayAppt } from '../../patient-dashboard.component';
import * as moment from "moment";
import { MessageService } from '../../../services/message.service';
import { Patient } from '../../../interface/patient';
import { Clinic } from '../../../interface/clinic';
import { LandingPageData } from '../../../interface/landingData';
import { DoctorService } from '../../../services/doctor-service/doctor.service';
import { pairs } from 'rxjs';
import { DynamicTitleService } from 'src/app/shared/UI_service/dynamic-title.service';

@Component({
  selector: 'app-your-appointments',
  templateUrl: './your-appointments.component.html',
  styleUrls: ['./your-appointments.component.less']
})
export class YourAppointmentsComponent implements OnInit {

  todayDate: number;
  allUpcomingAppt: Appointment[] = [];
  allPastAppt: Appointment[] = [];
  isLoadingPresc: boolean = false;
  patients: Patient[] = [];
  allClinics: Clinic[] = [];
  selectedPatient: Patient;
  constructor(private http: BaseHttpService, private msg: MessageService, private doctorService: DoctorService) { }

  ngOnInit(): void {
    this.patients = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));


    this.doctorService.data$.subscribe((d: LandingPageData) => {
      this.allClinics = d.clinics;
    })

    this.todayDate = new Date(moment().startOf('day').toString()).getTime();
    this.getUpcomingAppt(this.selectedPatient.patient_id);
  }


  patientChanged(item: Patient) {
    if (this.selectedPatient.patient_id != item.patient_id) {
      this.getUpcomingAppt(item.patient_id);
      this.selectedPatient = item;
    }
  }


  getUpcomingAppt(patientId: string) {
    this.allUpcomingAppt = [];
    this.allPastAppt = [];
    const dataToPost = { patient_id: patientId }
    this.http.makeAuthRequest<todayAppt>('POST', HttpConstants.appointment.getAllAppointments, dataToPost).subscribe(result => {

      if (result.data) {
        result.data.appointment.forEach(element => {

          if (element.startDateTime >= this.todayDate) {

            this.allUpcomingAppt = [...this.allUpcomingAppt, element];
          } else {
            this.allPastAppt = [...this.allPastAppt, element];
          }

        });
      }

    })

  }

  getPrescription(item: Appointment) {

    this.isLoadingPresc = true;
    const data = { doc_id: item.doc_id, patient_id: item.patient_id };

    this.http.makeAuthRequest<todayAppt>('POST', HttpConstants.appointment.getPrescriptionById, data).subscribe(result => {
      this.isLoadingPresc = false;

      if (result.errors.length > 0) {
        window.open(`https://migrate.digiscribe.in/downloadPdf/${item.doc_id}`, '_bank');
        return true;
      }

      if (result.data.appointment && result.data.appointment.length > 0) {

        console.log(`https://migrate.digiscribe.in/downloadPdf/${item.doc_id}`);

        window.open(`https://migrate.digiscribe.in/downloadPdf/${item.doc_id}`, '_bank');
        //  window.open(`https://migrate.digiscribe.in/pdf-renderer#/${item.doc_id}`,'_bank');
      } else {

        this.msg.createNotification('error', "We did not found any prescription on this appointment, Kindly consult with your doctor for the same.");
      }

    })

  }

}
