import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Patient } from 'src/app/interface/patient';
import { BaseHttpService } from 'src/app/services/base-http.service';
import { DoctorService } from 'src/app/services/doctor-service/doctor.service';
import { HttpConstants } from 'src/app/services/http-constants';
import { MessageService } from 'src/app/services/message.service';
import { MediaControlService } from 'src/app/shared/UI_service/media-control.service';
import { PatientService } from '../../../services/patient-service/patient.service';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.less']
})
export class MyProfileComponent implements OnInit {

  thisDevice: boolean = true;
  mobileNo: string;
  selectedPatient:Patient;
  patients:Patient[]= [];
  constructor(private _mediaControl: MediaControlService, private activatedRoute: ActivatedRoute, private router: Router, private baseHttpService: BaseHttpService, private doctorService: DoctorService, private msg: MessageService,private patientService:PatientService) { }


  personal_info: boolean = true;
  tabSwitch(param) {
    if (this.personalFormData) {
      this.personal_info = param;
    }

  }
  ngOnInit(): void {

    this.patients = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));

   
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._mediaControl.mediaPort()
  }
  personalFormData: any;
  nextForm(e) {
    console.log(e);
    this.personalFormData = e;
    this.tabSwitch(false);
  }
  createPatient(e) {

    this.selectedPatient.patient_title = this.personalFormData.patient_title;
    this.selectedPatient.patient_name = this.personalFormData.patient_name;
    this.selectedPatient.patient_last_name = `${this.personalFormData.patient_middle_name} ${this.personalFormData.patient_last_name}`;
    this.selectedPatient.patient_gender = this.personalFormData.patient_gender;
    this.selectedPatient.patient_marital_status = this.personalFormData.patient_marital_status;
    this.selectedPatient.patient_address_line_1 = this.personalFormData.patient_address_line_1;
    this.selectedPatient.patient_address_line_2 = this.personalFormData.patient_address_line_2;
    this.selectedPatient.patient_city = this.personalFormData.patient_city;
    this.selectedPatient.patient_state = this.personalFormData.patient_state;
    this.selectedPatient.patient_email = this.personalFormData.patient_email;

    this.selectedPatient.patient_pincode = this.personalFormData.patient_pincode;
    this.selectedPatient.patient_phone = this.mobileNo;
    this.selectedPatient.patient_age = this.personalFormData.patient_age;
    this.selectedPatient.patient_age_month = this.personalFormData.patient_age_month;

    if (e.allergicData) {
      this.selectedPatient.knownAllergies = e.allergicData;
    }

    this.selectedPatient.patient_weight_kgs = (e.patient_weight_kgs) ? parseInt(e.patient_weight_kgs) : 0;
    this.selectedPatient.patient_height = (e.patient_height) ? parseInt(e.patient_height) : 0;
    this.selectedPatient.patient_blood_type = e.patient_blood_group;




    this.baseHttpService.makeAuthRequest<boolean>("post", HttpConstants.patient.updatePateint, this.selectedPatient).subscribe((response) => {
      if (response.hasErrors()) {
        this.msg.createNotification('error', 'something went wrong please try again');
      } else {
        if (response.data) {
          this.msg.createMessage('info','Your profile is successfully updated')
          this.patientService.updatePatient(this.selectedPatient);
          localStorage.setItem( `${this.doctorService.username}-selectedPatient`, JSON.stringify(this.selectedPatient));
        }
        this.tabSwitch(true);

        // this.router.navigate(["../patient"], {
        //   relativeTo: this.activatedRoute,
        // });

      }
    });

  }
}

interface SignUpInterface {
  token: string;
  patient: Patient;
}