import { Component, OnInit } from '@angular/core';
import { Patient } from '../../../interface/patient';
import { BaseHttpService } from '../../../services/base-http.service';
import { DoctorService } from '../../../services/doctor-service/doctor.service';
import { HttpConstants } from '../../../services/http-constants';
import {
  HealthDocumentWrapper,
  HealthDocuments,
} from "../../../interface/health-document";
import * as moment from "moment";
import { NzDrawerPlacement } from 'ng-zorro-antd/drawer';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { uploadData } from '../health-update/health-update.component';
import { MessageService } from '../../../services/message.service';
import { Doctor } from '../../../interface/doctor';
import { LandingPageData } from '../../../interface/landingData';
import { DgPaymentServiceService } from '../../../services/patient-service/dg-payment-service.service';
import { DynamicTitleService } from 'src/app/shared/UI_service/dynamic-title.service';

@Component({
  selector: 'app-health-records',
  templateUrl: './health-records.component.html',
  styleUrls: ['./health-records.component.less']
})
export class HealthRecordsComponent implements OnInit {

  selectedPatient: Patient;
  patientList: Patient[] = [];
  healthDocumentMaster: HealthDocuments[] = [];
  documents: HealthDocumentWrapper[] = [];

  labReports: HealthDocumentWrapper[] = [];
  snapRx: HealthDocumentWrapper[] = [];
  otherDocuments: HealthDocumentWrapper[] = [];
  placement: NzDrawerPlacement = 'bottom';
  visible: boolean = false;
  uploadDocs: boolean = false;
  uploadText: string = 'Select / Upload Prescription';
  imgFilelist: NzUploadFile[] = [];
  authHeader: any;
  isDocsLoading: boolean = false;
  doctor: Doctor;
  constructor(private http: BaseHttpService, private doctorService: DoctorService, private msg: MessageService, private uploadService: DgPaymentServiceService) { }

  ngOnInit(): void {

    this.patientList = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.getPatientDocument(this.selectedPatient.patient_id);
    this.authHeader = {
      Authorization: "Bearer " + localStorage.getItem("token-" + this.doctorService.username),
    };

    this.doctorService.data$.subscribe((d: LandingPageData) => {
      this.doctor = d.doctor;

    })

  }


  patientChanged(item: Patient) {
    if (this.selectedPatient.patient_id != item.patient_id) {
      this.getPatientDocument(item.patient_id);
      this.selectedPatient = item;
    }
  }


  getPatientDocument(patientId: string) {

    this.labReports = [];
    this.otherDocuments = [];
    this.http.makeAuthRequest<HealthDocumentResponse>('POST', HttpConstants.doctor.getAllDocuments + patientId).subscribe(res => {

      if (res.hasErrors()) {
        alert(res.getErrorsText());
      } else {
        this.healthDocumentMaster = res.data.documents;
        this.manipulateData();
      }

      ;

    })


  }


  manipulateData() {
    const healthDocuments = this.healthDocumentMaster;
    this.documents = [];
    for (let index = 0; index < healthDocuments.length; index++) {
      const element = healthDocuments[index];
      element.digital_data.forEach((x) => {
        const doc = {
          name: x.name,
          url: x.photo,
          doc_id: element.doc_id,
          doctor_id: element.doctor_id,
          patient_id: element.patient_id,
          timestamp: element.timestamp,
          date: moment(element.timestamp).format("DD/MM/YYYY hh:mm a"),
          type: element.type,
          uploadedBy: (element.uploadedBy) ? element.uploadedBy : ''
        };
        this.documents.push(doc);
      });
    }

    this.snapRx = this.documents.filter((x) => x.type == "digital_pres");
    const audios = this.documents.filter((x) => x.type == "user_audio");
    const videos = this.documents.filter((x) => x.type == "user_video");
    const images = this.documents.filter((x) => x.type == "digital_phexam");
    this.labReports = this.documents.filter((x) => x.type == "user_labreport");
    const labReports: HealthDocumentWrapper[] = this.documents.filter((x) => x.type == "digital_labreport");
    this.labReports = this.labReports.concat(labReports);
    this.otherDocuments = this.otherDocuments.concat(audios, videos, images);



  }



  public tab: number = 1;
  tab_control(param) {
    this.tab = param;
  }
  public hasPanel: boolean = false;
  upload_panel(param) {
    this.hasPanel = param;
  }
  public change_ac_card: boolean = false;
  change_ac(param) {
    this.change_ac_card = param;
  }
  public file_control_menu: boolean = false;
  file_control(param) {
    this.file_control_menu = param;
  }

  fileType(filename) {

    return filename.split('.').pop();
  }

  openFile(url) {
    window.open(url, '_blank');

  }


  open(): void {
    this.visible = true;
  }

  close(): void {
    this.visible = false;

  }

  closeUploader() {
    this.uploadDocs = false;
    this.imgFilelist = []
  }

  type: string = 'digital_pres'
  openUploader(type) {

    this.type = type;
    this.uploadDocs = true;

    if (type === 'digital_pres') {
      this.uploadText = 'Select / Upload Prescrition';
    } else if (type === 'digital_labreport') {
      this.uploadText = 'Select / Upload lab report';

    } else if (type === 'digital_phexam') {
      this.uploadText = 'Select / Upload Ph. Exam';

    }


  }


  previewImage: string | undefined = '';
  previewVisible = false;

  handlePreview = async (file: NzUploadFile) => {
    if (!file.url && !file.preview) {
      file.preview = await this.getBase64(file.originFileObj!);
    }
    this.previewImage = file.url || file.preview;
    this.previewVisible = true;
  };


  getBase64(file: File): Promise<string | ArrayBuffer | null> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }

  async saveDocument() {

    let otherImgData: uploadData[] = [];
    if (this.imgFilelist.length > 0) {
      otherImgData = await this.imgFilelist.map((item) => {
        const fileObject: uploadData = { name: item.originFileObj.name, photo: item.response.url, thumb: item.response.url, mimeType: item.originFileObj.type, description: '', selected: false, text: '' }
        return fileObject;

      });
    } else {

      this.msg.createMessage('error', 'Please select file to upload');
      return false;
    }


    if (otherImgData.length > 0) {
      this.isDocsLoading = true;
      const body: HealthDocuments = {
        doc_id: `digital_pres_${this.selectedPatient.patient_id}_${Math.floor(new Date().getTime())}`,
        patient_id: this.selectedPatient.patient_id,
        channels: [this.selectedPatient.patient_id, this.doctor.uid],
        doctor_id: this.doctor.uid,
        appointment_id: '',
        digital_data: otherImgData,
        endtime: Math.floor(new Date().getTime()),
        isOnline: true,
        timestamp: Math.floor(new Date().getTime()),
        type: this.type,
        uploadedBy: 'patient'
      }

      this.saveDocumentOnServer(body);

    }

  }



  saveDocumentOnServer(data: HealthDocuments) {
    this.uploadService.createPrescription(data).subscribe((r) => {
      this.getPatientDocument(this.selectedPatient.patient_id);
      this.isDocsLoading = false;
      this.msg.createMessage('success', 'File saved.');
      this.visible = false;
      this.uploadDocs = false;
      this.imgFilelist = []
    })
  }


}

export interface HealthDocumentResponse {
  documents: HealthDocuments[];
}
