import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Appointment } from '../interface/appointment';
import { BaseHttpService } from '../services/base-http.service';
import { HttpConstants } from '../services/http-constants';
import * as moment from "moment";
import { NzModalService } from 'ng-zorro-antd/modal';
import { DoctorService } from '../services/doctor-service/doctor.service';
import { Patient } from '../interface/patient';
import { Clinic } from '../interface/clinic';
import { LandingPageData } from '../interface/landingData';
import { DynamicTitleService } from '../shared/UI_service/dynamic-title.service';

@Component({
  selector: 'app-patient-dashboard',
  templateUrl: './patient-dashboard.component.html',
  styleUrls: ['./patient-dashboard.component.less']
})
export class PatientDashboardComponent implements OnInit {
  todayDate = moment().format('ll');
  todaysAppointment: Appointment[] = [];
  isSpinning: boolean = false;
  patients: Patient[] = [];
  allClinics: Clinic[] = [];
  selectedPatient: Patient;
  constructor(private _router: Router, private http: BaseHttpService, private modal: NzModalService, private doctorService: DoctorService) { }
  scroll_cards: any[] = [
    {
      id: 1,
      title: 'Appointments',
      icon: 'Group 1694.svg',
      path: './Your-Appointments'
    },
    {
      id: 2,
      title: 'Health Records',
      icon: 'health-check.svg',
      path: './Health-Records'
    },
    // {
    //   id: 3,
    //   title: 'Message',
    // icon: 'communication.svg',
    // path: './Your-Messages'
    // },
    // {
    //   id: 4,
    //   title: 'My invoices',
    // icon: 'card_4.png',
    //   path: ''
    // },
    // {
    //   id: 5,
    //   title: 'My Health',
    //     icon: 'heart-rate.svg',
    //   path: ''
    // },
    {
      id: 6,
      title: 'my Heal Cart',
      icon: 'briefcase.svg',
      path: './cart'
    },
    {
      id: 7,
      title: 'My Orders',
      icon: 'Group 1698.svg',
      path: ''
    },
    {
      id: 8,
      title: 'My Profile',
      icon: 'Patient.svg',
      path: './myProfile'
    }
  ]
  ngOnInit(): void {

    this.doctorService.data$.subscribe((d: LandingPageData) => {
      this.allClinics = d.clinics;

    })
    this.patients = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.getUpcomingAppt(this.selectedPatient.patient_id);
  }

  getUpcomingAppt(patientId: string) {
    this.isSpinning = true;

    let now = moment().startOf('day').toString();
    const dataToPost = { timestamp: new Date(now).getTime(), patient_id: patientId }

    this.http.makeAuthRequest<todayAppt>('POST', HttpConstants.appointment.getTodaysAppointment, dataToPost).subscribe(result => {
      this.isSpinning = false;

      if (result.data) {
        this.todaysAppointment = result.data.appointment;
      }

    })

  }


  cards_page() {
    this._router.navigate(['./Select-Time'])

  }

  getApptTitle(item: Appointment) {

    console.log('11--')
    if (item.type == 'followup') {
      return 'Follow-up Reminder'
    } else if (item.isTelimedicineAppointment && item.isTelimedicineAppointment === true) {
      return 'Online Appt Reminder'
    } else {
      return 'Appointment Reminder'

    }
  }



  getData(e: Event) {
    console.log(e);
  }
}

export interface todayAppt {
  appointment: Appointment[]
} 
