import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { NzConfigService } from 'ng-zorro-antd/core/config';
import {
  NzNotificationPlacement,
  NzNotificationService,
} from 'ng-zorro-antd/notification';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { DynamicTitleService } from 'src/app/shared/UI_service/dynamic-title.service';
import { Doctor } from '../../../interface/doctor';
import { HealthDocuments } from '../../../interface/health-document';
import { LandingPageData } from '../../../interface/landingData';
import { Patient } from '../../../interface/patient';
import { DoctorService } from '../../../services/doctor-service/doctor.service';

import { AudioRecordingService } from '../../../services/patient-service/audio-recording.service';
import { DgPaymentServiceService } from '../../../services/patient-service/dg-payment-service.service';

@Component({
  selector: 'app-health-update',
  templateUrl: './health-update.component.html',
  styleUrls: ['./health-update.component.less'],
})
export class HealthUpdateComponent implements OnInit {
  blobUrl: any;
  audioBlob: any;
  urls: any[] = [];
  urlsOther: any[] = [];
  isVideoCapture: any = '';
  isUploading: boolean = false;
  uploadLimit: boolean = true;
  interval: any;
  additonalNotes: string = '';
  isRecording: boolean;
  recordedTime: string;
  labFileList: NzUploadFile[] = [];
  imgFilelist: NzUploadFile[] = [];
  selectedPatient: Patient;
  doctor: Doctor;
  authHeader: any;
  constructor(
    private notification: NzNotificationService,
    private audioRecordingService: AudioRecordingService,
    private sanitizer: DomSanitizer,
    private doctorService: DoctorService,
    private uploadService: DgPaymentServiceService,
    private router: Router,
    private activedRouter: ActivatedRoute
  ) { }


  emoji_status: any[] = [
    { id: 1, value: 'Great', img: 'Image 10.svg' },
    { id: 2, value: 'Ok', img: 'Image 11.svg' },
    { id: 3, value: 'Bad', img: 'Image 12.svg' },
    { id: 4, value: 'Horrible', img: 'Image 13.svg' },
  ]
  active_emoji: number = 0;
  getEmojiStatus(param) {
    this.active_emoji = param;
  }


  ngOnInit(): void {

    this.audioRecordingService.recordingFailed().subscribe(() => {
      this.isRecording = false;
    });

    this.audioRecordingService.getRecordedTime().subscribe((time) => {
      this.recordedTime = time;
    });

    this.audioRecordingService.getRecordedBlob().subscribe((data) => {
      this.blobUrl = this.sanitizer.bypassSecurityTrustUrl(URL.createObjectURL(data.blob));
      this.audioBlob = data.blob;
    });

    this.authHeader = {
      Authorization: "Bearer " + localStorage.getItem("token-" + this.doctorService.username),
    };
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));

    this.doctorService.data$.subscribe((d: LandingPageData) => {

      this.doctor = d.doctor;

    })
  }



  startRecording() {
    if (!this.isRecording) {
      this.isRecording = true;
      this.audioRecordingService.startRecording();
    }
  }

  abortRecording() {
    if (this.isRecording) {
      this.isRecording = false;
      this.audioRecordingService.abortRecording();
    }
  }

  stopRecording() {
    if (this.isRecording) {
      this.audioRecordingService.stopRecording();
      this.isRecording = false;
      console.log(this.blobUrl);
    }
  }

  clearRecordedData() {
    this.blobUrl = null;
  }


  onSelectFileVideo(event) {
    if (event.target.files && event.target.files[0]) {
      const filesAmount = event.target.files.length;
      for (let i = 0; i < filesAmount; i++) {
        const reader = new FileReader();

        reader.onload = (event: any) => {
          event.target.type;
          console.log(event.target.result);
          this.isVideoCapture = event.target.result;
        };

        reader.readAsDataURL(event.target.files[i]);
      }
    }
  }

  previewImage: string | undefined = '';
  previewVisible = false;

  handlePreview = async (file: NzUploadFile) => {
    if (!file.url && !file.preview) {
      file.preview = await this.getBase64(file.originFileObj!);
    }
    this.previewImage = file.url || file.preview;
    this.previewVisible = true;
  };


  getBase64(file: File): Promise<string | ArrayBuffer | null> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }

  isLoading: boolean = false;

  async checkFile() {


    let labData: uploadData[] = [];
    let otherImgData: uploadData[] = [];

    this.isLoading = true;
    if (this.labFileList.length > 0) {
      labData = await this.labFileList.map((item) => {
        const fileObject: uploadData = { name: item.originFileObj.name, photo: item.response.url, thumb: item.response.url, mimeType: item.originFileObj.type, description: '', selected: false, text: '' }
        return fileObject;

      });
    }

    if (this.imgFilelist.length > 0) {
      otherImgData = await this.imgFilelist.map((item) => {
        const fileObject: uploadData = { name: item.originFileObj.name, photo: item.response.url, thumb: item.response.url, mimeType: item.originFileObj.type, description: '', selected: false, text: '' }
        return fileObject;

      });
    }


    if (labData.length > 0) {

      const body: HealthDocuments = {
        doc_id: `digital_lab_${this.selectedPatient.patient_id}_${Math.floor(new Date().getTime())}`,
        patient_id: this.selectedPatient.patient_id,
        channels: [this.selectedPatient.patient_id, this.doctor.uid],
        doctor_id: this.doctor.uid,
        appointment_id: '',
        digital_data: labData,
        endtime: Math.floor(new Date().getTime()),
        isOnline: true,
        timestamp: Math.floor(new Date().getTime()),
        type: 'digital_labreport',
        uploadedBy: 'patient'
      }
      this.saveDocumentOnServer(body);

    }

    if (otherImgData.length > 0) {
      const body: HealthDocuments = {
        doc_id: `digital_phexam_${this.selectedPatient.patient_id}_${Math.floor(new Date().getTime())}`,
        patient_id: this.selectedPatient.patient_id,
        channels: [this.selectedPatient.patient_id, this.doctor.uid],
        doctor_id: this.doctor.uid,
        appointment_id: '',
        digital_data: otherImgData,
        endtime: Math.floor(new Date().getTime()),
        isOnline: true,
        timestamp: Math.floor(new Date().getTime()),
        type: 'digital_phexam',
        uploadedBy: 'patient'
      }

      this.saveDocumentOnServer(body);



    }


    setTimeout(() => {

      this.isLoading = false;
      this.router.navigate(['../'], {
        replaceUrl: true,
        relativeTo: this.activedRouter,
      });

    }, 2000)



  }

  skipNow() {
    this.router.navigate(['../'], {
      replaceUrl: true,
      relativeTo: this.activedRouter,
    });

  }

  saveDocumentOnServer(data: HealthDocuments) {
    this.uploadService.createPrescription(data).subscribe((r) => {
      console.log(r);

    })
  }

}

export interface uploadData {
  name: string;
  photo: string;
  thumb: string;
  mimeType: string;
  text: string;
  selected: boolean;
  description: string

}