import { Component, OnInit } from '@angular/core';
import { CustomIconsService } from '../../../../shared/UI_service/custom-icons.service';
import { allIcons } from '../../../../shared/icon/custom-icon';
import { customerAddressDetails } from '../../../../interface/orderDetails';
import { Patient } from '../../../../interface/patient';
import { DoctorService } from '../../../../services/doctor-service/doctor.service';
import { BaseHttpService } from '../../../../services/base-http.service';
import { customerDetails } from '../../../../interface/customerDetails';
import { ActivatedRoute, Router } from '@angular/router';
import { NzModalService } from 'ng-zorro-antd/modal';


@Component({
  selector: 'app-confirm-address',
  templateUrl: './confirm-address.component.html',
  styleUrls: ['./confirm-address.component.less']
})
export class ConfirmAddressComponent implements OnInit {

patientAddress:customerDetails
selectedPatient:Patient; 

  location = this._CustomIcons.setIcon(allIcons.location);
  Edit = this._CustomIcons.setIcon(allIcons.Edit)
  trash = this._CustomIcons.setIcon(allIcons.trash)
  isSpining: boolean;
  selectedAddress:any;
  constructor(private _CustomIcons: CustomIconsService,private doctorService:DoctorService,private http:BaseHttpService, private router:Router,private activedRouter:ActivatedRoute,private modal: NzModalService) { }

  ngOnInit(): void {
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.getPatientAddress(this.selectedPatient.patient_id);

    this.selectedAddress = JSON.parse(localStorage.getItem(`selectedAddress-${this.selectedPatient.patient_id}`)) || '';
  }

  getPatientAddress(patientId:string){

    this.isSpining = true;
    const data = {"patientId":"digi-10005"}

    this.http.makePartnerRequest<customerDetails>({method:'POST',url:'/v1/customer/findAllAddressByDigiId',data:data}).subscribe(res => {
      this.isSpining  = false;
      this.patientAddress = res.data;
    })

  }


  selectedThis(item) {

    localStorage.setItem(`selectedAddress-${this.selectedPatient.patient_id}`,JSON.stringify(item));

    this.router.navigate(['../apptTime'], { 
      relativeTo: this.activedRouter,
      replaceUrl: true,
    }); 
  }

  addNewAddress(){

    this.router.navigate(['../addAddress/'+this.patientAddress.customerId], { 
      relativeTo: this.activedRouter,
      replaceUrl: true,
    }); 



  }

  editAddress(item){

    this.router.navigate(['../addAddress/'+this.patientAddress.customerId], { 
      relativeTo: this.activedRouter,
      replaceUrl: true,
      state: { apptData: item }

    }); 
  }
  

  showDeleteConfirm(item): void {
    this.modal.confirm({
      nzTitle: 'Are you sure delete this Address?',
      nzOkText: 'Yes',
      nzOkType: 'danger',
      nzOnOk: () => this.deletePatientAddress(item),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });
  }

  deletePatientAddress(item) {

    this.isSpining = true;

    this.http.makePartnerRequest<customerDetails>({method:'POST',url:'/v1/customer/deleteCustomerAddressById',data:{"customerAddressId":item.customerAddressId}}).subscribe(res => {
      this.isSpining  = false;
      const index = this.patientAddress.customerAddresses.indexOf(item);
      if (index >= 0) {
        this.patientAddress.customerAddresses.splice(index, 1);
      }


    })


  }

}
