import { Component, OnInit } from '@angular/core';
import { orderDetails, orderItems } from '../../../../interface/orderDetails';
import { Patient } from '../../../../interface/patient';
import { DoctorService } from '../../../../services/doctor-service/doctor.service';
import { CustomIconsService } from '../../../../shared/UI_service/custom-icons.service';

@Component({
  selector: 'app-order-summary',
  templateUrl: './order-summary.component.html',
  styleUrls: ['./order-summary.component.less']
})
export class OrderSummaryComponent implements OnInit {
  medications_data: any[] = [
    {
      name: 'D3 HD 60K Tablet',
      quantity: '3 strips x Rs 350 / strip',
      price: 1050
    },
    {
      name: 'Mintop 10 Hair Restore Formula',
      quantity: '1 pack x Rs 1350 / pack',
      price: 1350
    },
    {
      name: 'Lacto Calamine Oil Balance Lotion',
      quantity: '1 bottle x Rs 350 / bottle',
      price: 350
    },
    {
      name: 'Zincovit Tablet',
      quantity: '1 strip x Rs 350 / strip',
      price: 350
    },
    {
      name: 'Telma 80 CT',
      quantity: '1 strip x Rs 350 / strip',
      price: 350
    },
  ]
  diagnosticsTest: any[] = [
    {
      name: '17 OH Progesterone',
      quantity: 'Home Collection by Dr Lal Path Labs',
      price: 350
    },
    {
      name: 'VENOUS COLOUR DOPPLER BOTH LEG',
      quantity: 'Center Visit at Shaksham Imaging and Diagnostics ',
      price: 3150
    },
  ]

  selectedPatient:Patient;
  itemsAddedCart:orderDetails[] = [];
  finalTotalAmount:number = 0;
  homeCollectionCharges:number = 100;
  platFormFee:number = 0;
  isActiveList:boolean = true;
  constructor(private _IconsService: CustomIconsService,private doctorService:DoctorService) { }
  ngOnInit(): void {
    
    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.itemsAddedCart =  JSON.parse(localStorage.getItem('cartData-'+this.selectedPatient.patient_id)) || []
      
    this.itemsAddedCart.forEach(element => {
      this.count(element.orders);
    });
    
  }

  count(array: orderItems[]) {

    this.finalTotalAmount = this.finalTotalAmount  + array.reduce((r, a) => {
      return r + a.amountRecieveable;
    }, 0);

    this.calculateFee()
  }
  public PaymentPage: number;
  paymentMetod:string;
  

  calculateFee(){

    var platFormPrice = Number(this.finalTotalAmount + this.homeCollectionCharges + 14.75);
    var platFormPriceFee = (platFormPrice * 2) / 100;
    var tax = platFormPriceFee * 0.18;
    this.platFormFee =
      Number(platFormPriceFee.toFixed(2)) + Number(tax.toFixed(2));
  }

  next() {
    this.isActiveList = false;
    this.PaymentPage = 1;
  }
}
