import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { OwlOptions, SlidesOutputData } from 'ngx-owl-carousel-o';
import { Patient } from '../../interface/patient';
import { DoctorService } from '../../services/doctor-service/doctor.service';
import { PatientService } from '../../services/patient-service/patient.service';
import { allIcons } from '../../shared/icon/custom-icon';
import { CustomIconsService } from '../../shared/UI_service/custom-icons.service';

@Component({
  selector: 'app-patient-card',
  templateUrl: './patient-card.component.html',
  styleUrls: ['./patient-card.component.less']
})
export class PatientCardComponent implements OnInit {

  @Output() patientChanged = new EventEmitter<Patient>();
  Edit = this.CustomIcons.setIcon(allIcons.pen)
  patients:Patient[]= [];
  constructor( private CustomIcons: CustomIconsService,private doctorService:DoctorService,private patientService:PatientService) { }

  ngOnInit(): void {

    this.patients = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));

    const selectedPatient:Patient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    const index =  this.patients.map(  (x) => {return x.patient_id; }).indexOf(selectedPatient.patient_id);
    this.customOptions.startPosition = index;
  }


  
  getData(data: SlidesOutputData) {
    this.patientService.setSelectedPatient(this.patients[data.startPosition]);
    this.patientChanged.emit(this.patients[data.startPosition]); 

  }


  customOptions: OwlOptions = {
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: false,
    navSpeed: 700,
    navText: ['', ''],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 2
      },
      940: {
        items: 4
      }
    },
    startPosition:0,
    nav: false
  }
  
  
}
