import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Patient } from '../../../../interface/patient';
import { BaseHttpService } from '../../../../services/base-http.service';
import { DoctorService } from '../../../../services/doctor-service/doctor.service';
import { customerAddressDetails } from '../../../../interface/orderDetails'
import { MessageService } from '../../../../services/message.service';


@Component({
  selector: 'app-new-address',
  templateUrl: './new-address.component.html',
  styleUrls: ['./new-address.component.less']
})
export class NewAddressComponent implements OnInit {
  radioValue = 'A';
  addressForm: FormGroup;
  selectedPatient:Patient;
  initData:addressType[];
  stateData:stateData[] ;
  cityData:cityData[] ;
  localityData:any[]= []
  customerId:number;
  isSpining:boolean;
  constructor( private fb: FormBuilder,private http:BaseHttpService,private doctorService:DoctorService,
    private cdf: ChangeDetectorRef,private router:Router,private activedRouter:ActivatedRoute,private msg:MessageService

    ) { }

  ngOnInit(): void {

    this.getAddressInitData();
    
     this.activedRouter.params.subscribe(params => {
      this.customerId = +params['id']; //

   });

    this.addressForm = this.fb.group({
     
      patient_address_line_1: ['',[Validators.required]],
      patient_address_line_2: ['',[Validators.required]],
      landmark: [''],
      patient_state: ['', [Validators.required]],
      patient_city: ['', [Validators.required]],
      patient_pincode: [null, [Validators.required,Validators.maxLength(6),Validators.minLength(6),Validators.pattern('^[0-9]*$')]],
      locality:[''],
      addressType:['',[Validators.required]]


    });

  }
  selectedValue:string= '';



  requiredChange(required: boolean): void {
    if (!required) {
      this.addressForm.get('locality')!.clearValidators();
      this.addressForm.get('locality')!.markAsPristine();
    } else {
      this.addressForm.get('locality')!.setValidators(Validators.required);
      this.addressForm.get('locality')!.markAsDirty();
    }
    this.addressForm.get('locality')!.updateValueAndValidity();
  }



  getAddressInitData() {

   this.http.makePartnerRequest<stateData[]>({method:'GET',url:'/v1/store/findAllState'}).subscribe(res => {
    
    if(res && res.data) {
          this.stateData = res.data;
        }

        this.cdf.detectChanges()
    })



    this.http.makePartnerRequest<any>({method:'GET',url:'/v1/customer/getPatientPortalInitData'}).subscribe(res => {
      
        if(res && res.data) {
          this.initData = res.data.addressType;
        }
    })


  }


  getCityByStateId(item:stateData) {

      this.cityData = [];
      this.http.makePartnerRequest<cityData[]>({method:'POST',url:'/v1/store/findAllCityByStateId',data:{"stateId":item.stateId}}).subscribe(res => {
       if(res && res.data) {
        this.cityData = res.data;
        this.cdf.detectChanges()
      }
  })

  }


  getLocalityByCityId(item:cityData) {

      if(item.cityId === 1) {
        this.requiredChange(true);
      }  else {
        this.requiredChange(false);

      }


    console.log(item);
    
      this.localityData = [];
      this.http.makePartnerRequest<cityData[]>({method:'POST',url:'/v1/store/findAllLocalityByCityId',data:{"cityId":item.cityId}}).subscribe(res => {
       if(res && res.data) {
        this.localityData = res.data;
        if(res.data.length === 0) {
          this.requiredChange(false);

        }
        this.cdf.detectChanges()
      }
  })

  }


  submitForm(): void {
    for (const i in this.addressForm.controls) {
      this.addressForm.controls[i].markAsDirty();
      this.addressForm.controls[i].updateValueAndValidity();
    }


    if(this.addressForm.valid) {
      console.log(this.addressForm.value);
      this.isSpining = true;

      const dataToPost = {
        customerId:this.customerId,
        patient_address_line_1:this.addressForm.value.patient_address_line_1,
        patient_address_line_2:this.addressForm.value.patient_address_line_2,
        patient_city:this.addressForm.value.patient_city.cityName,
        patient_state:this.addressForm.value.patient_state.stateName,
        patient_pincode:this.addressForm.value.patient_pincode.toString(),
        customerAddressTypeId:this.addressForm.value.addressType.customerAddressTypeId
      };
      console.log(dataToPost);

      this.http.makePartnerRequest({method:'POST',url:'/v1/customer/createPatientAddress',data:dataToPost}).subscribe(res => {
        this.isSpining = false;

        if(res && res.data) {
          
          this.router.navigate(['../../address'], { 
            relativeTo: this.activedRouter,
            replaceUrl: true,
              
          }); 
       } else {

        this.msg.createMessage('error','Error while update data please try again');
        return false;
       }
    })

    }
  }


}

export interface stateData {

  stateId:number;
  stateName:string

}


export interface cityData {

  cityId:number;
  cityName:string

}
export interface addressType { 

CustomerAddressTypeName:string,customerAddressTypeId:number,
}

