import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Patient } from '../../../../interface/patient';
import { MessageService} from '../../../../services/message.service'

@Component({
  selector: 'app-health-info',
  templateUrl: './health-info.component.html',
  styleUrls: ['./health-info.component.less'],
})
export class HealthInfoComponent implements OnInit {
  @Output() submitFinalForm = new EventEmitter<Boolean>();
  @Input() patient:Patient;
  selectedValue = null;
  bloodGroups: string[] = ['O+', 'O-', 'A+', 'A-', 'B+', 'B-', 'AB+', 'AB-'];
  healthForm: FormGroup;

  weightArray: string[] = [];
  heightArray: string[] = [];
  isLoading:boolean = false;
  

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {

    this.healthForm = this.fb.group({
      patient_weight_kgs: [this.patient.patient_weight_kgs],
      patient_height: [this.patient.patient_height],
      patient_blood_group: [this.patient.patient_blood_type],
      isAllergic: [false],
      isIllness: [false],
      illnessBrief: [],
      allergicData: [],
      healthHistory: [],
      agree:[true,[Validators.required,Validators.requiredTrue]]
    });

    for (let i = 3; i < 130; i++) {
      this.weightArray.push(i.toString());
    }
    for (let i = 50; i < 200; i++) {
      this.heightArray.push(i.toString());
    }
  }

  async submitForm() {
    console.log(this.healthForm.value);


    this.isLoading = true;
   const res =  await this.submitFinalForm.emit(this.healthForm.value);
   console.log(res);
  }
}
