import { Component, Input, OnInit, Output } from '@angular/core';
import { MediaControlService } from 'src/app/shared/UI_service/media-control.service';
import * as moment from 'moment';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Patient } from '../../../../interface/patient';
import { ActivatedRoute } from '@angular/router';
import { EventEmitter } from '@angular/core';
import { MessageService} from '../../../../services/message.service'
@Component({
  selector: 'app-personal-info',
  templateUrl: './personal-info.component.html',
  styleUrls: ['./personal-info.component.less'],
})
export class PersonalInfoComponent implements OnInit {
  thisDevice: boolean = true;
  validateForm: FormGroup;
  allTitle: string[] = ["Mr", "Mrs", "Ms", "Dr", "Baby"];
  marriedStatus: string[] = ["Single", "Married", "Widow", "Divorcee"];
  isLoading:boolean = false;
  @Output() nextForm = new EventEmitter<Boolean>();
  @Input() patient:Patient

  constructor(
    private _mediaControl: MediaControlService,
    private fb: FormBuilder,
    private activatedRoute:ActivatedRoute,
    private msg:MessageService
  ) {

    this.activatedRoute.params.subscribe((params) => {
      console.log(params);
      const mobile = params.mobile;


    })

  }

  allMonth: Array<{label:string,value:number}> = [
    {label:'Jan',value:1},
    {label:'Feb',value:2},
    {label:'Mar',value:3},
    {label:'Apr',value:4},
    {label:'May',value:4},
    {label:'June',value:6},
    {label:'July',value:7},
    {label:'Aug',value:8},
    {label:'Sept',value:9},
    {label:'Oct',value:10},
    {label:'Nov',value:11},
    {label:'Dec',value:12},
  ];

  all_year: any[] = [];
  count = 1950;

  // img url
  imageUrl: string = 'assets/img/placeholder.png';
  fileUpload: File = null;
  // file upload
  fileInput(file: FileList) {
    this.fileUpload = file.item(0);
    var reander = new FileReader();
    reander.onload = (event: any) => {
      this.imageUrl = event.target.result;
    };
    reander.readAsDataURL(this.fileUpload);
  }





  ngOnInit(): void {
    this.years();

const currentYear:any = moment().format('yyyy')

    const year  = (currentYear  - this.patient.patient_age);

    this.validateForm = this.fb.group({
      patient_title: [ this.patient.patient_title, [Validators.required]],
      patient_marital_status: [this.patient.patient_marital_status],
      patient_phone: [this.patient.patient_phone, [Validators.required,Validators.maxLength(10),Validators.minLength(10),Validators.pattern('^[0-9]*$')]],
      patient_dob_year: [year.toString()],
      patient_dob_month: [this.patient.patient_age_month,],
      patient_email: ['', [Validators.required,Validators.email]],
      patient_name: [this.patient.patient_name, [Validators.required]],
      patient_middle_name: [''],
      patient_last_name: [this.patient.patient_last_name, [Validators.required]],
      patient_gender: [this.patient.patient_gender, [Validators.required]],
      patient_address_line_1: [this.patient.patient_address_line_1],
      patient_address_line_2: [this.patient.patient_address_line_2],
      landmark: [''],
      patient_state: [this.patient.patient_state, [Validators.required]],
      patient_city: [this.patient.patient_city, [Validators.required]],
      patient_pincode: [this.patient.patient_pincode, [Validators.required,Validators.maxLength(6),Validators.minLength(6),Validators.pattern('^[0-9]*$')]],
      phoneNumberPrefix:['+91'],
      notification:[true],
      patient_age:[this.patient.patient_age],
      patient_age_month:[this.patient.patient_age_month],
      patient_age_days:[this.patient.patient_age_day],

    });

    if(this.patient.patient_img_path && this.patient.patient_img_path !=  '') {
      this.imageUrl = this.patient.patient_img_path;
    }

  }

  submitForm(): void {

      const currentYear = moment().format('yyyy') ;
      const year = this.validateForm.get('patient_dob_year').value;
       const month = this.validateForm.get('patient_dob_month').value;
        if(year) {
         const years:any  = ( parseInt(currentYear) - parseInt(year))
         this.validateForm.get('patient_age').setValue(years);
      }    

      if(month) {
        const months:any  = ( 12 - parseInt(month))
        this.validateForm.get('patient_age_month').setValue(months);
     }    



    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsDirty();
      this.validateForm.controls[key].updateValueAndValidity();
    }

    if(this.validateForm.valid) {
      console.log(this.validateForm.value);

      this.nextForm.emit(this.validateForm.value);

      
    }
  }



  

  years() {
    const year = moment().format('yyyy');
    const current: number = parseInt(year) - this.count;
    for (let i = 0; i <= current; i++) {
      this.all_year.push(`${this.count++}`);
    }
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._mediaControl.mediaPort();
  }
}
