import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FirstTimeVisitorComponent } from './page/first-time-visitor/first-time-visitor.component';
import { SelectTimeComponent } from './page/select-time/select-time.component';
import { PatientDashboardComponent } from './patient-dashboard.component';
import { ConfirmAppointmentComponent } from './page/confirm-appointment/confirm-appointment.component';
import { HealthUpdateComponent } from './page/health-update/health-update.component';
import { YourAppointmentsComponent } from './page/your-appointments/your-appointments.component';
import { HealthRecordsComponent } from './page/health-records/health-records.component';
import { YourMessagesComponent } from './page/your-messages/your-messages.component';
import { YourHealCartComponent } from '../patient-dashboard/page/your-heal-cart/your-heal-cart.component';
import { ConfirmCartComponent } from './page/your-heal-cart/confirm-cart/confirm-cart.component';
import { ConfirmAddressComponent } from './page/your-heal-cart/confirm-address/confirm-address.component';
import { NewAddressComponent } from './page/your-heal-cart/new-address/new-address.component';
import { ConfirmTimingComponent } from './page/your-heal-cart/confirm-timing/confirm-timing.component';
import { OrderSummaryComponent } from './page/your-heal-cart/order-summary/order-summary.component';
import { PaymentMethodComponent } from './page/your-heal-cart/payment-method/payment-method.component';
import { PatientRootComponent } from './patient-root/patient-root.component'
import { CartRootComponent } from './page/your-heal-cart/cart-root/cart-root.component';
import { MyProfileComponent } from './page/my-profile/my-profile.component';

const routes: Routes = [
  {
    path: '', component: PatientRootComponent,
    children: [
  { path: '', component: PatientDashboardComponent},
  { path: 'Visitor', component: FirstTimeVisitorComponent },
  { path: 'book-appointment', component: SelectTimeComponent },
  { path: 'book-appointment/:id', component: SelectTimeComponent },

  { path: 'Confirm', component: ConfirmAppointmentComponent },
  { path: 'Health-Update', component: HealthUpdateComponent },
  { path: 'Your-Appointments', component: YourAppointmentsComponent },
  { path: 'Health-Records', component: HealthRecordsComponent },
  { path: 'Your-Messages', component: YourMessagesComponent },
  { path: 'payment', component: PaymentMethodComponent },

      { path: 'Confirm', component: ConfirmAppointmentComponent },
      { path: 'Health-Update', component: HealthUpdateComponent },
      { path: 'Your-Appointments', component: YourAppointmentsComponent },
      { path: 'Health-Records', component: HealthRecordsComponent },
      { path: 'Your-Messages', component: YourMessagesComponent },
      { path: 'myProfile', component: MyProfileComponent },
      { path: 'confirm-order', component: ConfirmCartComponent },
      { path: 'address', component: ConfirmAddressComponent },
      { path: 'addAddress', component: NewAddressComponent },
      { path: 'apptTime', component: ConfirmTimingComponent },
      { path: 'orderSummary', component: OrderSummaryComponent },
      { path: 'payment', component: PaymentMethodComponent },

      {
        path: 'cart', component: CartRootComponent, children: [
          { path: '', component: YourHealCartComponent },
          { path: 'confirm-order', component: ConfirmCartComponent },
          { path: 'address', component: ConfirmAddressComponent },
          { path: 'addAddress/:id', component: NewAddressComponent },
          { path: 'apptTime', component: ConfirmTimingComponent },
          { path: 'orderSummary', component: OrderSummaryComponent },
          { path: 'payment', component: PaymentMethodComponent },

          { path: 'confirm-order', component: ConfirmCartComponent },
          { path: 'Confirm_Address', component: ConfirmAddressComponent },
          { path: 'add-Address', component: NewAddressComponent },
          { path: 'Confirm_Timing', component: ConfirmTimingComponent },
          { path: 'confirm_timing', component: ConfirmTimingComponent },
          { path: 'Order_Summary', component: OrderSummaryComponent },
          { path: 'Payment_Method', component: PaymentMethodComponent }
        ]
      },
    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PatientDashboardRoutingModule { }
