import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { orderDetails } from '../../../../interface/orderDetails';
import { Patient } from '../../../../interface/patient';
import { BaseHttpService } from '../../../../services/base-http.service';
import { DoctorService } from '../../../../services/doctor-service/doctor.service';

@Component({
  selector: 'app-confirm-timing',
  templateUrl: './confirm-timing.component.html',
  styleUrls: ['./confirm-timing.component.less']
})
export class ConfirmTimingComponent implements OnInit {

  constructor(private doctorService:DoctorService,private http:BaseHttpService,private router:Router,private activedRouter :ActivatedRoute) { }
  public Drug: boolean = false;
  itemsAddedCart:orderDetails[]= [];
  selectedPatient:Patient;
  finalSlots:remotimeSlots;
  isSpining:boolean;
  ngOnInit(): void {

    this.selectedPatient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
    this.itemsAddedCart =  JSON.parse(localStorage.getItem('cartData-'+this.selectedPatient.patient_id)) || []

    
    //this.itemsAddedCart[0].partnerDetails.isRemoteAPI
    if(this.itemsAddedCart.length > 0) {
       this.getSlotFromServer(this.itemsAddedCart[0],this.itemsAddedCart[0].selectedSlotDateTime.selectedDate);
    }

  }
  dateChanged(date,item:orderDetails,i:number) {


    this.getSlotFromServer(item,date);

  }

  selectSlot(item:orderDetails,time) {

    item.selectedSlotDateTime.slotStartTime = time;

    console.log(this.itemsAddedCart);

  }


  getSlotFromServer(item,date) {

    this.isSpining = true;
    const data  = {"partnerCityId":4,"locality":"ASIF NAGAR","visitDate":date}

    this.http.makePartnerRequest<remotimeSlots>({ method: 'POST', url: '/v1/lalpath/getAllAvailableSlot', data: data }).subscribe(res => {
      this.isSpining = false;
     this.finalSlots = res.data;

    })

  }


  finalOrder() {

  localStorage.setItem('cartData-'+this.selectedPatient.patient_id, JSON.stringify(this.itemsAddedCart))

    this.router.navigate(['../orderSummary'], { 
      relativeTo: this.activedRouter,
      replaceUrl: true,
        
    }); 

  }


}


export interface remotimeSlots {

    date:string;
    phlebo_slots:Array<{slot:string,name:string,price:number,type:string,count:number}>

}