import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NzModalService } from 'ng-zorro-antd/modal';
import { allIcons } from 'src/app/shared/icon/custom-icon';
import { CustomIconsService } from 'src/app/shared/UI_service/custom-icons.service';
import { Appointment } from '../../interface/appointment';
import { Clinic } from '../../interface/clinic';
import { Patient } from '../../interface/patient';
import { BaseHttpService } from '../../services/base-http.service';
import { HttpConstants } from '../../services/http-constants';
import { MessageService } from '../../services/message.service';
import { slotInterface } from '../page/select-time/select-time.component';

@Component({
  selector: 'app-appointment-card',
  templateUrl: './appointment-card.component.html',
  styleUrls: ['./appointment-card.component.less'],
})
export class AppointmentCardComponent implements OnInit {
  @Input() todaysAppointment: Appointment[] = [];
  @Input() patients: Patient[];
  @Input() allClinics: Clinic[];
  constructor(
    private modal: NzModalService,
    private baseHttpService: BaseHttpService,
    private msg: MessageService,
    private router: Router,
    private activedRouter: ActivatedRoute,
    private _IconsService: CustomIconsService
  ) { }

  ngOnInit(): void { }

  cancelAppt(item: Appointment) {
    this.modal.confirm({
      nzTitle: 'Are you sure cancel this appointment?',
      nzContent:
        '<small style="color: red;">once the appointment is cancelled.it not converted into confim again.</small>',
      nzOkText: 'Yes',
      nzOkType: 'danger',
      nzOnOk: () => this.cancelApptRequest(item),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel'),
    });
  }
  cancelApptRequest(appointment: Appointment) {
    const body = {
      appointmentId: appointment.doc_id,
      patientId: appointment.patient_id,
      patient_phone: appointment.patient_phone,
      doctorId: appointment.doctor_id,
      startDateTime: appointment.startDateTime,
    };

    this.baseHttpService
      .makeAuthRequest<boolean>(
        'POST',
        HttpConstants.appointment.cancelAppointment,
        body
      )
      .subscribe((response) => {
        if (response.errors.length) {
          this.msg.createMessage('error', 'failed to cancel the appointment.');
          return;
        } else {
          appointment.isCancelled = true;
          this.msg.createMessage('success', 'Appointment Cancelled');
        }
      });
  }

  changeAppt(appt: Appointment) {
    let time;

    const clinic = this.allClinics.filter((item) => {
      return item.doc_id === appt.clinic_id;
    });

    if (clinic.length > 0) {
      time = clinic[0].timings.filter((item) => {
        return item.doc_id === appt.clinic_timimg;
      });
    }

    const patient = this.patients.filter((item) => {
      return item.patient_id === appt.patient_id;
    });

    const userSelectedSlot: slotInterface = {
      slotTime: appt.startDateTime,
      endSlot: appt.endDateTime,
      status: 'block',
    };

    const appData = {
      clinic: clinic ? clinic[0] : '',
      time: time ? time[0] : '',
      slot: userSelectedSlot,
      date: new Date(appt.startDateTime),
      patient: patient[0],
      isTelemedicine: appt.isTelimedicineAppointment,
      document_id: appt.doc_id
    };

    this.router.navigate(['../book-appointment'], {
      relativeTo: this.activedRouter,
      state: { apptData: appData }
    });
  }

  confirmAppt(appt: Appointment) {

    const patient = this.patients.filter((item) => {
      return item.patient_id === appt.patient_id;
    });

    const appData = {
      clinic: null,
      time: null,
      slot: null,
      date: new Date(appt.startDateTime),
      patient: patient[0],
      isTelemedicine: (appt.isTelimedicineAppointment) ? true : false,
      document_id: appt.doc_id
    };

    this.router.navigate(['../book-appointment'], {
      relativeTo: this.activedRouter,
      state: { apptData: appData }
    });

  }


  joinCall(item: Appointment) {
    window.open(`https://api.digiscribe.in/dashboard/${item.patient_id}/${item.patient_name}/${item.doc_id}`, "_blank");
  }

  // icon
  video = this._IconsService.setIcon(allIcons.video)
  rechedule = this._IconsService.setIcon(allIcons.rechedule)
  directions = this._IconsService.setIcon(allIcons.directions)
  pay = this._IconsService.setIcon(allIcons.pay)
  cancel = this._IconsService.setIcon(allIcons.cancel)

}
