import { Component, OnInit } from '@angular/core';
import { Appointment } from '../../../interface/appointment';
import { Clinic } from '../../../interface/clinic';
import { ClinicTime } from '../../../interface/clinic-timing';
import { LandingPageData } from '../../../interface/landingData';
import { Patient } from '../../../interface/patient';
import { DoctorService } from '../../../services/doctor-service/doctor.service';
import * as moment from "moment";
import { MessageService } from '../../../services/message.service';
import { Route } from '@angular/compiler/src/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PatientService } from '../../../services/patient-service/patient.service';
import { DynamicTitleService } from 'src/app/shared/UI_service/dynamic-title.service';


@Component({
  selector: 'app-select-time',
  templateUrl: './select-time.component.html',
  styleUrls: ['./select-time.component.less']
})
export class SelectTimeComponent implements OnInit {
  Pick_slot = 1;
  radioValue_one = 1;
  patients:Patient[]= [];
  selectedPatient:Patient;
  appointment:Appointment;
  isTelemedicine:boolean = true;
  allClinic:Clinic[]= [];
  selectedClinic:Clinic;
  selectedTime:ClinicTime;
  currentStime: number;
  loopTime: number;
  currentTimeSlot: number = 15;
  currentEtime: number;
  doctorSlots: any[]= [];
  selectedDateCalender = new Date();
  clinicTimings:any[]= []
  routerLinkData:any;
  rescheduledDocId:string = '';
  constructor(private doctorService:DoctorService,private msg:MessageService,public  router:Router,private route:ActivatedRoute,private patientService:PatientService) {

    if(this.router.getCurrentNavigation().extras.state){
      this.routerLinkData = this.router.getCurrentNavigation().extras.state.apptData;
    }
   }

  ngOnInit(): void {

    this.patients = JSON.parse(localStorage.getItem("patients" + this.doctorService.username));
    if(this.patients.length === 1) {
      this.selectedPatient = this.patients[0];

    } else  {

      const selectedPatient:Patient = JSON.parse(localStorage.getItem(`${this.doctorService.username}-selectedPatient`));
      const patient  = this.patients.filter((item) =>{
          return item.patient_id === selectedPatient.patient_id
       })
      
       this.selectedPatient = patient[0];
    }

   this.doctorService.data$.subscribe((d:LandingPageData) => {
   this.allClinic = d.clinics;
    
   const clinic = this.allClinic.filter( (item) => {
      return item.isVirtualClinic === true;

    })
    
    this.selectedClinic = clinic[0];


    if(this.routerLinkData){
      this.selectedClinic = this.routerLinkData.clinic;
      this.selectedTime= this.routerLinkData.time;
      this.isTelemedicine= this.routerLinkData.isTelemedicine;
      this.userSelectedSlot= this.routerLinkData.slot;
      this.selectedDateCalender = new Date(this.routerLinkData.date);
       this.rescheduledDocId  = this.routerLinkData.document_id;
      const patient  = this.patients.filter((item) =>{
        return item.patient_id === this.routerLinkData.patient.patient_id
     })
    
     this.selectedPatient = patient[0];

      
    }

    this.dateChanged(this.selectedDateCalender);

    

  });
  }


  toggleType() {
    this.isTelemedicine = (this.isTelemedicine) ? false : true;

    if(this.isTelemedicine){
      const clinic = this.allClinic.filter( (item) => {
        return item.isVirtualClinic === true;
  
      })
       this.selectedClinic = clinic[0];

    } else {
    const clinic = this.allClinic.filter( (item) => {
      return !item.isVirtualClinic;

    })
     this.selectedClinic = clinic[0];
   
  }

  this.dateChanged(this.selectedDateCalender);
  this.userSelectedSlot = null;
  this.selectedTime = null;
  this.finalSlots  = []; 
  }

  dateChanged(date) {
     this.clinicTimings = [];
    this.selectedDateCalender = new Date(date);
    const selectDay = moment(this.selectedDateCalender).weekday();
    if(this.selectedClinic){
    const clinicTimeOfDay  =  this.selectedClinic.clinicTimings.filter( (item) =>{
        return  ( item.disableDays.indexOf(selectDay) !=  -1)        
      })
      this.clinicTimings  = clinicTimeOfDay;
    }
    if(this.selectedTime) {
      this.showSlots(this.selectedTime);
    }

  }

  getDoctorSlots(date:Date){


  }

  changePatient(item:Patient) {

  this.selectedPatient = item;
  //console.log("selectedPatientOnChange");
  this.patientService.setSelectedPatient(this.selectedPatient);

  }

  changeClinic(item:Clinic) {
    console.log(item);
    this.selectedClinic = item;
    this.selectedTime = null;
    this.userSelectedSlot = null;
    this.dateChanged(this.selectedDateCalender);

  }

  changeTime(item:ClinicTime) {
    if(item != this.selectedTime)
    this.selectedTime = item;
    this.showSlots(item);
  }

  selectSlot(slot:slotInterface){
    this.userSelectedSlot = slot;
  }


  finalSlots:slotInterface[]= []
  Slots:slotInterface[]= [];
  userSelectedSlot:slotInterface;

  showSlots(data) {
    var dataClinic = data;
    var disabledDay = [];
    this.Slots = [];
    this.finalSlots = [];
    this.userSelectedSlot = null;

    var currentDate = new Date(this.selectedDateCalender);
    var day = moment(currentDate).format("DD");
    var month = moment(currentDate).format("MM");

    var year = moment(currentDate).format("YYYY");

    var combinationDate3 =
      month + "/" + day + "/" + year + " " + data.starttime;
    var combinationDate4 = month + "/" + day + "/" + year + " " + data.endtime;

    var StartTime =
      moment(combinationDate3, "MM/DD/YYYY HH:mm").valueOf() / 1000;
    var EndTimeS =
      moment(combinationDate4, "MM/DD/YYYY HH:mm").valueOf() / 1000;
    var currentTimeDate = Math.floor(new Date().getTime()) / 1000;

    if (currentTimeDate > EndTimeS) {
      //$(".time_all_slot").fadeOut();
      this.finalSlots = [];
  this.msg.createMessage('info','Sorry Clinic closed for today');

      //$(".loading").hide();
      return false;
    }

    if (dataClinic.monStatus === false) {
      disabledDay.push(1);
    }
    if (dataClinic.tueStatus === false) {
      disabledDay.push(2);
    }
    if (dataClinic.wedStatus === false) {
      disabledDay.push(3);
    }
    if (dataClinic.thuStatus === false) {
      disabledDay.push(4);
    }
    if (dataClinic.friStatus === false) {
      disabledDay.push(5);
    }
    if (dataClinic.satStatus === false) {
      disabledDay.push(6);
    }
    if (dataClinic.sunStatus === false) {
      disabledDay.push(7);
    }

    this.currentStime = StartTime;
    this.currentEtime = EndTimeS;
    this.loopTime = StartTime;

    this.startLoop();
  }

  startLoop() {
    var time = this.currentTimeSlot;

    if (
      this.loopTime == this.currentEtime - 60 * time ||
      this.loopTime > this.currentEtime - 60 * time
    ) {
      this.Slots.push({ slotTime: this.currentStime, status: "" });

      this.Slots.sort((x, y) => {
        return x.slotTime - y.slotTime;
      });

      var data = this.Slots;

      for (var c = 0; c < data.length; c++) {
        var found = this.doctorSlots.some((el) => {
          //    alert(data[c].slotTime);
          return el.startDateTime === data[c].slotTime * 1000;
        });
        if (!found) {
          if (new Date().getTime() / 1000 > data[c].slotTime) {
          } else {
            this.finalSlots.push({
              slotTime: data[c].slotTime * 1000,
              endSlot: data[c].slotTime + 60 * time * 1000,
              status: "avail",
            });
          }
        } else {
          // finalSlots1.push({slotTime:data[c].slotTime,status:'block'});
        }
      }

    } else {
      time = this.currentTimeSlot;
      this.Slots.push({ slotTime: this.loopTime + time * 60, status: "block" });

      this.loopTime = this.loopTime + time * 60;
      // this.setState({loopTime: this.state.loopTime + 15*60}, () => {

      this.startLoop();
      //});
    }
  }
  

  bookAppointment()
  {

    if(!this.selectedClinic) {
      this.msg.createMessage('error','Please select clinic');
      return false;
    }

    if(!this.selectedTime) {
      this.msg.createMessage('error','Please select appointment time');
      return false;
    }
    if(!this.userSelectedSlot) {
      this.msg.createMessage('error','Please select appointment time');
      return false;
    }


    const appData = {clinic:this.selectedClinic,time:this.selectedTime,slot:this.userSelectedSlot,date:this.selectedDateCalender,patient:this.selectedPatient,isTelemedicine:this.isTelemedicine,document_id:this.rescheduledDocId}
    this.router.navigate(['../Confirm'], { 
      relativeTo: this.route,
      replaceUrl: true,
      state: { apptData: appData } 
    });  }




}


export interface slotInterface{

  slotTime:number;
  endSlot?:number;
  status:string;
}